.line-top {
  display: block;
  position: absolute;
  left: 0;
  top: -11px;
  width: 100%;
  height: 3px;
  background-color: #fff;
  transition: top 0.2s linear 0.2s;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}